a {
  color: #2dc997;
}

a:hover,
a:active,
a:focus {
  color: #2dca98;
  outline: none;
  text-decoration: none;
}

p {
  padding: 0;
  margin: 0 0 30px 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  margin: 0 0 20px 0;
  padding: 0;
}

/* Section */
section {
  overflow: hidden;
}
.section-header .section-title {
  font-size: 32px;
  color: #111;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  margin-bottom: 5px;
}
.section-header .section-description {
  text-align: center;
  padding-bottom: 40px;
  color: #999;
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

#hero {
  width: 100%;
  height: 100vh;
  background: url(../../../public/Images/9wkdtx6.jpg) top center;
  background-size: cover;
  position: relative;
}

@media (min-width: 1024px) {
  #hero {
    background-attachment: fixed;
  }
}

#hero:before {
  content: '';
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
}

#hero .hero-container {
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

#hero h1 {
  margin: 30px 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  line-height: 56px;
  text-transform: uppercase;
  color: #fff;
}

@media (max-width: 768px) {
  #hero h1 {
    font-size: 28px;
    line-height: 36px;
  }
}

#hero h2 {
  color: #eee;
  margin-bottom: 50px;
  font-size: 24px;
}

@media (max-width: 768px) {
  #hero h2 {
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 30px;
  }
}

#hero .btn-get-started {
  font-family: 'Poppins', sans-serif;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 8px 28px;
  border-radius: 50px;
  transition: 0.5s;
  margin: 10px;
  border: 2px solid #fff;
  color: #fff;
}

#hero .btn-get-started:hover {
  background: #2dc997;
  border: 2px solid #2dc997;
}

/* NAVBAR SECTION */
.navBg {
  width: 100%;
  position: fixed;
  z-index: 999;
  background-color: #000;
}
.BrandName {
  margin-left: 5%;
  padding: 1%;
}
.navElements {
  margin-right: 5%;
  align-items: center;
}
.flickPlay {
  color: red;
}

/* ABOUT SECTION */
#about {
  background: #fff;
  padding: 80px 0;
}

#about .about-container .background {
  min-height: 300px;
  background: url(../../../public/Images/azadi.png) no-repeat;
  margin-bottom: 10px;
}

#about .about-container .content {
  background: #fff;
}

#about .about-container .title {
  color: #333;
  font-weight: 700;
  font-size: 32px;
}

@media (max-width: 768px) {
  #about .about-container .title {
    padding-top: 15px;
  }
}

#about .about-container p {
  line-height: 26px;
}

#about .about-container p:last-child {
  margin-bottom: 0;
}

#about .about-container .icon-box {
  background: #fff;
  background-size: cover;
  padding: 0 0 30px 0;
}

#about .about-container .icon-box .icon {
  float: left;
  background: #fff;
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  border-radius: 50%;
  border: 2px solid #2dc997;
}

#about .about-container .icon-box .icon i {
  color: #2dc997;
  font-size: 24px;
}

#about .about-container .icon-box .title {
  margin-left: 80px;
  font-weight: 500;
  margin-bottom: 5px;
  font-size: 18px;
  text-transform: uppercase;
}

#about .about-container .icon-box .title a {
  color: #111;
}

#about .about-container .icon-box .description {
  margin-left: 80px;
  line-height: 24px;
  font-size: 14px;
}

/* Facts Section */
#facts {
  background: #f7f7f7;
  padding: 80px 0 60px 0;
}

#facts .counters span {
  font-size: 48px;
  display: block;
  color: #2dc997;
}

#facts .counters p {
  padding: 0;
  margin: 0 0 20px 0;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
}

/* REVIEWS SECTION */
body {
  background: #212121;
}
.review {
  margin-bottom: 5%;
}
#testimonial_095 {
  overflow: hidden;
  min-height: 375px;
  position: relative;
  background: #212121;
}

#testimonial_095 > .carousel-inner {
  width: 100%;
  margin: auto;
}

#testimonial_095 > .carousel-inner:hover {
  cursor: -moz-grab;
  cursor: -webkit-grab;
}

#testimonial_095 > .carousel-inner:active {
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

#testimonial_095 > .carousel-inner > .carousel-item {
  overflow: hidden;
}

.testimonial_095_indicators > .carousel-indicators {
  left: 0;
  margin: 0;
  width: 100%;
  font-size: 0;
  height: 20px;
  bottom: 15px;
  padding: 0 5px;
  display: block;
  overflow-x: auto;
  overflow-y: hidden;
  position: absolute;
  text-align: center;
  white-space: nowrap;
}

.testimonial_095_indicators > .carousel-indicators li {
  padding: 0;
  width: 10px;
  height: 10px;
  border: none;
  text-indent: 0;
  margin: 2px 3px;
  cursor: pointer;
  display: inline-block;
  background: #fff;
  -webkit-border-radius: 100%;
  border-radius: 100%;
}

.testimonial_095_indicators > .carousel-indicators .active {
  padding: 0;
  width: 10px;
  height: 10px;
  border: none;
  margin: 2px 3px;
  background: #1ed600;
  -webkit-border-radius: 100%;
  border-radius: 100%;
}

.testimonial_095_indicators > .carousel-indicators::-webkit-scrollbar {
  height: 3px;
}

.testimonial_095_indicators > .carousel-indicators::-webkit-scrollbar-thumb {
  background: #ffffff;
  -webkit-border-radius: 0;
  border-radius: 0;
}

.testimonial_095_control_button .carousel-control-next,
.testimonial_095_control_button .carousel-control-prev {
  top: 175px;
  opacity: 1;
  width: 40px;
  bottom: auto;
  height: 40px;
  font-size: 10px;
  cursor: pointer;
  font-weight: 700;
  overflow: hidden;
  line-height: 38px;
  text-shadow: none;
  text-align: center;
  position: absolute;
  background: 0 0;
  border: 2px solid #fff;
  text-transform: uppercase;
  -webkit-border-radius: 100%;
  border-radius: 100%;
  -webkit-box-shadow: none;
  box-shadow: none;
  -webkit-transition: all 0.6s cubic-bezier(0.3, 1, 0, 1);
  transition: all 0.6s cubic-bezier(0.3, 1, 0, 1);
}

.testimonial_095_control_button .carousel-control-prev {
  left: 7%;
  right: auto;
}

.testimonial_095_control_button .carousel-control-next {
  right: 7%;
  left: auto;
}

.testimonial_095_control_button .carousel-control-next:hover,
.testimonial_095_control_button .carousel-control-prev:hover {
  color: #ffffff;
  background: #1ed600;
  border: 2px solid #ffffff;
}

.testimonial_095_header > h5,
.testimonial_095_slide > h5 > a,
.testimonial_095_slide > p {
  text-shadow: none;
  font-family: Raleway, sans-serif;
  -webkit-font-smoothing: antialiased;
}

.testimonial_095_header {
  top: 0;
  left: 0;
  bottom: 0;
  width: 550px;
  display: block;
  margin: 30px auto;
  text-align: center;
  position: relative;
}

.testimonial_095_header > h5 {
  color: #f2f2f2;
  font-size: 30px;
  font-weight: 600;
  position: relative;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.testimonial_095_header > h5 > span {
  color: #1ed600;
  margin: 0 10px;
  position: relative;
}

.testimonial_095_header > h5 > span:after {
  left: 0;
  right: 0;
  bottom: 0;
  top: auto;
  height: 1px;
  content: '';
  width: 100%;
  margin: auto;
  position: absolute;
  background: #1ed600;
}

.testimonial_095_slide,
.testimonial_095_slide > a {
  right: 0;
  margin: auto;
  text-align: center;
  position: relative;
  top: 0;
  left: 0;
}

.testimonial_095_slide {
  bottom: 0;
  width: 70%;
  padding: 20px;
}

.testimonial_095_slide > a {
  width: 50px;
  height: 50px;
  display: block;
  color: #f2f2f2;
  font-size: 18px;
  line-height: 46px;
  border: 3px solid #f2f2f2;
  -webkit-border-radius: 100%;
  border-radius: 100%;
}

.testimonial_095_slide > a:after {
  top: 0;
  left: 0;
  padding: 0;
  opacity: 0;
  width: 100%;
  content: '';
  height: 100%;
  position: absolute;
  -webkit-border-radius: 100%;
  border-radius: 100%;
  -webkit-transform: scale(0.9);
  -ms-transform: scale(0.9);
  transform: scale(0.9);
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1);
}

.testimonial_095_slide > a:hover:after {
  -webkit-animation: psBoxShadowEffect_2 2s ease-out;
  animation: psBoxShadowEffect_2 2s ease-out;
}

.testimonial_095_slide > p {
  color: #f2f2f2;
  font-size: 14px;
  margin: 40px 0 20px;
}

.testimonial_095_slide > p > a {
  color: #1ed600;
  padding: 2px 5px;
  text-decoration: none;
  -webkit-transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}

.testimonial_095_slide > p > a:hover {
  color: #fff;
  background: #aa914f;
}

.testimonial_095_slide > h5 > a {
  padding: 3px;
  color: #1ed600;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;
  border: 1px solid transparent;
  -webkit-transition: border 0.5s cubic-bezier(0.39, 0.575, 0.565, 1);
  transition: border 0.5s cubic-bezier(0.39, 0.575, 0.565, 1);
}

.testimonial_095_slide > h5 > a:hover {
  border: 1px solid #aa914f;
}

@media only screen and (max-width: 480px) {
  .testimonial_095_control_button .carousel-control-next,
  .testimonial_095_control_button .carousel-control-prev {
    display: none;
  }
}

@media only screen and (max-width: 480px) {
  .testimonial_095_header {
    width: 95%;
  }
}

@media only screen and (max-width: 480px) {
  .testimonial_095_header > h5 {
    font-size: 20px;
  }
}

@media only screen and (max-width: 480px) {
  .testimonial_095_slide {
    width: 98%;
    padding: 5px;
  }
}

@media (min-width: 481px) and (max-width: 767px) {
  .testimonial_095_control_button .carousel-control-prev {
    left: 2%;
  }
}

@media (min-width: 481px) and (max-width: 767px) {
  .testimonial_095_control_button .carousel-control-next {
    right: 2%;
  }
}

@media (min-width: 481px) and (max-width: 767px) {
  .testimonial_095_header {
    width: 95%;
  }
}

@media (min-width: 481px) and (max-width: 767px) {
  .testimonial_095_slide {
    width: 98%;
    padding: 5px;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .testimonial_095_control_button .carousel-control-prev {
    left: 5%;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .testimonial_095_control_button .carousel-control-next {
    right: 5%;
  }
}

@keyframes psBoxShadowEffect_2 {
}

